import { ProcessStepElement, ProcessStepElementBase } from '@jumoo/processing';
import { uSyncRestore } from '@jumoo/usync-expansions';
import { customElement, html } from '@umbraco-cms/backoffice/external/lit';

@customElement(uSyncRestore.steps.restoreReportStep)
export class uSyncRestoreReportElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	connectedCallback(): void {
		super.connectedCallback();

		const changes = (this.results?.results['changes'] as number) ?? 0;

		console.log('changes', changes);

		this.dispatchEvent(
			new CustomEvent('pipeline-valid', {
				bubbles: true,
				composed: true,
				detail: {
					valid: changes != 0,
				},
			}),
		);

		this.dispatchEvent(
			new CustomEvent('pipeline-modal-update', {
				bubbles: true,
				composed: true,
				detail: {
					progressLabel: 'Restore',
				},
			}),
		);
	}

	render() {
		const actions = this.results?.results['actions'] as any[];

		return html`<usync-results .results=${actions}></usync-results>`;
	}
}

export default uSyncRestoreReportElement;
